						'        [0, 6],\n',
						'        [6, 7],\n',
						'        [7, 8],  # neck - l_hip - l_knee - l_ankle\n',
						'        [0, 12],\n',
						'        [12, 13],\n',
						'        [13, 14],  # neck - r_hip - r_knee - r_ankle\n',
						'    ]\n',
