						'\n',
						'    img = np.transpose(img, (2, 0, 1))[\n',
						'        None,\n',
						'    ]\n',
						'    infer_request.infer({input_tensor_name: img})\n',
						'    # A set of three inference results is obtained\n',
						'    results = {\n',
