						'\n',
						'\n',
						'def draw_poses(frame, poses_2d, scaled_img, use_popup):\n',
						'    """\n',
						'    Draw 2D pose overlays on the image to visualize estimated poses.\n',
						'    Joints are drawn as circles and limbs are drawn as lines.\n',
