						'import collections\n',
						'import time\n',
						'from IPython import display\n',
						'import cv2\n',
						'import numpy as np\n',
						'\n',
						'\n',
						'# Main processing function to run object detection.\n',
						'def run_object_detection(\n',
						'    source=0,\n',
						'    flip=False,\n',
						'    use_popup=False,\n',
						'    skip_first_frames=0,\n',
						'    det_model=ov_yolo_int8_model,\n',
